import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import './ProjectsList.scss';

class ProjectsList extends Component {
  state = {
    projects: []
  }
  getList = () => {
    const result = axios.
    get('https://hobby-api.herokuapp.com/api')
      .then((res) => this.setState({projects: res.data}))
      .catch((error) => console.log(error));
    console.log(result);
  }
  render() {
    const projectList = this.state.projects.map((project, i) => {
      return (
        <div key={i}>
          <h4>{project.title}</h4>
          <p>{project.description}</p>
          <p>{project.amount}</p>
          <p>{project.username}</p>
          <p>{project.email}</p>
        </div>
      )
    })
    return (
      <div className='ProjectsList'>
        {projectList}
        <p>Projects will be soon...</p>
        <p>If you have a project, please{' '}
          <Link className='Link' to='/project-registration'>Register</Link>
          {' '}your project(hobby).
        </p>
        <button onClick={this.getList}>Click</button>
      </div>
    );
  }
}

export default ProjectsList;